









export default {
	name: 'image-preview',
	props: {
		src: { //图片地址
		    type: String,
		    default: ''
		},
	    show: { //是否显示模态框
	        type: [Boolean, String],
	        default: false
	    }
	},
	data() {
		return {
			isOpen: false,
			showAnimation:false,
			inWatchTimeout:false,
			inMethodsTimeout:false
		};
	},
	watch: {
	    show(val) {
	        if(this.inWatchTimeout){
	            return
	        }
	        if (val) {
	        	this.isOpen = val
	            setTimeout(() => {
	            	this.showAnimation = val
	            }, 50)
	        } else {
	            this.showAnimation = val
	            this.inWatchTimeout = true
	        	setTimeout(() => {
	        		this.isOpen = val
	                this.inWatchTimeout = false
	        	}, 200)
	        }
	    }
	},
	created() {
		this.isOpen = this.show
		setTimeout(() => {
			this.showAnimation = this.show
		}, 50)
	},
	methods:{
		closeModal() {
		    this.showAnimation = false
		    this.inMethodsTimeout = true
		    setTimeout(() => {
		    	this.isOpen = false
		        this.inMethodsTimeout = false
		        this.$emit('close')
		    }, 200)
		}
	},
	onLoad() {
	    
	}
}
