




































import mInput from '@/components/m-input.vue'

export default {
	components: {mInput},
	name: 'enroll',
	props: {
		kind: { //类别
		    type: String,
		    default: ''
		},
		title: { //标题
		    type: String,
		    default: ''
		},
		subTitle: { //小标题
		    type: String,
		    default: ''
		},
	    show: { //是否显示模态框
	        type: [Boolean, String],
	        default: false
	    }
	},
	data() {
		return {
			name:'',
			mobile:'',
			isOpen: false,
			showAnimation:false,
			inWatchTimeout:false,
			inMethodsTimeout:false
		};
	},
	watch: {
	    show(val) {
	        if(this.inWatchTimeout){
	            return
	        }
	        if (val) {
	        	this.isOpen = val
	            setTimeout(() => {
	            	this.showAnimation = val
	            }, 50)
	        } else {
	            this.showAnimation = val
	            this.inWatchTimeout = true
	        	setTimeout(() => {
	        		this.isOpen = val
	                this.inWatchTimeout = false
	        	}, 200)
	        }
	    }
	},
	created() {
		this.isOpen = this.show
		setTimeout(() => {
			this.showAnimation = this.show
		}, 50)
	},
	methods:{
		closeModal() {
		    this.showAnimation = false
		    this.inMethodsTimeout = true
		    setTimeout(() => {
		    	this.isOpen = false
		        this.inMethodsTimeout = false
		        this.$emit('close')
		    }, 200)
		},
		submit:function(){
			if(this.name == ''){
				uni.showToast({
					icon:'none',
					title:'请输入您的称呼'
				})
				return;
			}
			if(this.name.length > 20){
				uni.showToast({
					icon:'none',
					title:'您的称呼内容超出（限制20个字符）'
				})
				return;
			}
			if(this.mobile == ''){
				uni.showToast({
					icon:'none',
					title:'请输入您的手机号'
				})
				return;
			}
			if(this.mobile.length != 11){
				uni.showToast({
					icon:'none',
					title:'请输入有效的手机号'
				})
				return;
			}
			let that = this;
			uni.request({
				url:this.$http.contextPath + 'base/user-consult/save',
				method:'POST',
				data:{
					kind:this.kind,
					name:this.name,
					mobile:this.mobile
				},
				success(res) {
					if(that.$http.success(res)){
						uni.showToast({
							title:'您的请求已提交，稍后客服会跟您联系'
						})
						that.closeModal();
					}
				}
			});
		}
	},
	onLoad() {
	    
	}
}
