







































import fIcon from '@/components/m-icon/f-icon.vue'
import mpWeixin from '@/static/data/mp-weixin.js';
import {mapState,mapGetters,mapMutations} from 'vuex'

export default {
	components: {
	    fIcon
	},
	name: 'login-modal',
	props: {
	    title: { //标题
	        type: String,
	        default: ''
	    },
	    content: String, //提示的内容
	    align: { //content 的对齐方式left/center/right
	        type: String,
	        default: 'left'
	    },
	    cancelText: { //取消按钮的文字，默认为"取消"
	        type: String,
	        default: '取消'
	    },
	    cancelColor: { //取消按钮颜色
	        type: String,
	        default: '#333333'
	    },
	    confirmText: { //确定按钮的文字，默认为"确定"
	        type: String,
	        default: '确定'
	    },
	    confirmColor: { //确认按钮颜色
	        type: String,
	        default: '#007aff'
	    },
	    showCancel: { //是否显示取消按钮，默认为 true
	        type: [Boolean, String],
	        default: true
	    },
	    show: { //是否显示模态框
	        type: [Boolean, String],
	        default: false
	    }
	},
	data() {
		return {
			providerList: [],
			hasProvider:false,
			
			isOpen: false,
			showAnimation:false,
			inWatchTimeout:false,
			inMethodsTimeout:false
		};
	},
	computed: {
		...mapState(['redirect']),
		...mapGetters(['loginCheck'])
	},
	watch: {
	    show(val) {
	        if(this.inWatchTimeout){
	            return
	        }
	        if (val) {
	        	this.isOpen = val
	            setTimeout(() => {
	            	this.showAnimation = val
	            }, 50)
	        } else {
	            this.showAnimation = val
	            this.inWatchTimeout = true
	        	setTimeout(() => {
	        		this.isOpen = val
	                this.inWatchTimeout = false
	        	}, 200)
	        }
	    }
	},
	created() {
		this.isOpen = this.show
		setTimeout(() => {
			this.showAnimation = this.show
		}, 50)
	},
	methods:{
		...mapMutations(['loginSuccess']),
		initProvider() {
		    const filters = ['weixin', 'qq', 'weibo'];
		    uni.getProvider({
		        service: 'oauth',
		        success: (res) => {
		            if (res.provider && res.provider.length) {
		                for (let i = 0; i < res.provider.length; i++) {
		                    if (~filters.indexOf(res.provider[i])) {
		                        this.providerList.push({
		                            value: res.provider[i],
		                            image: '/static/img/logos/' + res.provider[i] + '.png'
		                        });
		                    }
		                }
		                this.hasProvider = true;
		            }
		        },
		        fail: (err) => {
		            console.error('获取服务供应商失败：' + JSON.stringify(err));
		        }
		    });
		},
		oauth(provider) {
			let that = this;
		    uni.login({
		        provider: provider,
		        success: (e) => {
		            if(provider == 'weixin'){
		            	//获取openID
		            	uni.request({
		            		url: mpWeixin.openIdUrl + e.code,
		            		method: 'GET',
		            		success: (sess) =>{
		            			that.fastLogin(provider, sess.data.openid);
		            		},
		            		fail(e) {
		            			uni.showToast({
		            				title:'获取信息错误'
		            			})
		            		}
		            	})
		            }
		        },
		        fail: (err) => {
		            console.error('授权登录失败：' + JSON.stringify(err));
		        }
		    });
		},
		fastLogin(provider, openId){
			let param = {
				provider:provider
			};
			if(provider == 'weixin'){
				param.openIdWeixin = openId;
			}else if(provider == 'weibo'){
				param.openIdWeibo = openId;
			}else if(provider == 'qq'){
				param.openIdQq = openId;
			}else{
				return;
			}
			let that = this;
			uni.request({
				url:this.$http.contextPath + 'account/login',
				method:'POST',
				data:param,
				success(res) {
					if(that.$http.success(res)){
						that.loginSuccess(res.data.data);
						that.$route.enter(that.loginCheck(that.redirect));
					}else{
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
						});
					}
				}
			})
		},
		bindTouchmove() {},
		closeModal() {
		    this.showAnimation = false
		    this.inMethodsTimeout = true
		    setTimeout(() => {
		    	this.isOpen = false
		        this.inMethodsTimeout = false
		        this.$emit('close')
		    }, 200)
		},
		bindLogin() {
			this.closeModal();
			uni.navigateTo({
				url:"../../pages/login/login"
			});
		},
		bindReg() {
			this.closeModal();
			uni.navigateTo({
				url:"../../pages/reg/reg"
			})
		}
	},
	onLoad() {
	    this.initProvider();
	}
}
